<template>
  <div>
    <div class="wrap">
      <a @click="$router.go(-1)"><</a>
      <div class="content">
        <h2></h2>
      </div>
    </div>
    <ul>
      <li><span></span><input type="text"></li>
      <li><span></span><input type="text"></li>
      <li style="display: flex;justify-content: space-between;" ><span></span><p  id="p" @click="handleClick"><span>{{choose}}</span><i class="iconfont  icon-jiantouxia1" style="float: right;color:#CCCCCC;"></i></p></li>
      <li><span></span><input type="text" onkeyup="value=value.replace(/[^\d]/g,'')"></li>
    </ul>
    <p style="line-height: 0.8rem;font-size: 0.24rem;font-weight: 600;padding-left: 0.2rem;"></p>
    <div class="authcode">
      <input type="text" onkeyup="value=value.replace(/[^\d]/g,'')" placeholder="">
      <div style="border-left: 1px solid #F67930;color:#F67930;width: 30%;padding-left: 0.3rem;text-align: center;line-height: 0.56rem;font-size: 0.28rem;"><p @click="countdown" v-show="sendAuthCode" >{{list}}</p><p v-show="!sendAuthCode" style="color:#7E7E7E;">{{auth_time}}</p></div>
    </div>
    <div class="end">
      <div class="suretwo" :class="">
        <p></p>
      </div>
      <div class="lastdiv">
        <p>:</p>
        <p>1;</p>
        <p>2;</p>
        <p>3;</p>
        <p>4;</p>
      </div>
    </div>
    <mt-popup v-model="popupVisible" position="bottom" class="popupdiv">
      <ul >
        <li v-for="(con, index) in bank" @click="surebank(index)">{{con.name}}</li>
      </ul>
    </mt-popup>
  </div>
</template>

<script>
  // import { popup } from 'mint-ui'
    export default {
        name: "linkaccount",
      data() {
          return {
            list: '',
            sendAuthCode:true,
            auth_time:0,
            popupVisible:false,
            choose: '',
            bank: [
              {name: ''},
              {name: ''},
              {name: ''},
              {name: ''},
              {name: ''},
              {name: ''},
              {name: ''},
              {name: ''},
            ]
          }
      },
      methods: {
        countdown:function () {
          this.sendAuthCode = false;
          this.auth_time = 60;
          var auth_timetimer =  setInterval(()=>{
            this.auth_time--;
            if(this.auth_time<=0){
              this.sendAuthCode = true;
              clearInterval(auth_timetimer);
            }
          }, 1000)
        },
        handleClick: function() {
          this.popupVisible = true
        },
        surebank(num){
          this.popupVisible = false;
          this.choose = this.bank[num].name
        }
      }
    }
</script>

<style scoped>
.wrap{
  background: #452E94;
  width:100%;
  height: 1.11rem;
  display:flex;
  justify-content: space-between;
}
.wrap a{
  display:block;
  font-size:0.6rem;
  margin:0.16rem;
  height: 0.56rem;
  width: 1.2rem;
  border-radius:2rem;
  text-align:center;
  background: #452E94;
  color:#e6d9f3;
}
.content{
  width: 100%;
}
.content h2{
  color: #E0E0EA;
  text-align: center;
  font-size: 0.35rem;
  line-height:1.11rem;
  padding-right:1.2rem;
}

  input{
    font-size: 0.28rem;
  }
  .popupdiv{
    width: 100%;
  }
  .popupdiv li{
    border-bottom: 1px solid #CCCCCC;
    text-align: center;
    font-size: 0.28rem;
  }
  .popupdiv ul{
    margin-top: 0;
  }
  ul{
    margin-top: 0.3rem;
    background: white;
  }
  li{
    padding: 0.2rem 0.3rem;
  }
  li p{
    width: 80%;
  }
  li i{
    font-size: 0.28rem;
  }
  #p span{
    /*font-size: 0.24rem;*/
    font-weight: 300;
    color:black;
  }
  li span{
    font-weight: 600;
    font-size: 0.28rem;

  }
  li input{
    margin-left: 0.3rem;
    width: 75%;
  }
  .suretwo{
    width: 100%;
    border-radius: 5px;
    background: #CDCDCD;
    text-align: center;
    height: 1rem;
    line-height: 1rem;
    margin-bottom: 0.5rem;
    color:white;
    font-size: 0.34rem;
  }
  .authcode{
    height: 0.96rem;
    padding: 0.2rem 0.4rem;
    background: white;
    display: flex;
    justify-content: space-between;
    font-weight: 700;
  }
  .lastdiv p{
    font-size: 0.24rem;
    padding-top: 0.1rem;
  }
  .end{
    padding: 0.42rem 0.62rem 0 0.62rem;
  }
</style>



