<template>
  <div>
    <div class="wrap">
      <a v-on:click="back"><</a>
      <div class="content">
          <h2>{{ title }}</h2>
      </div>
      <button class="shezhi" @click="queren"></button>
    </div>

    <ul class="qiangduiul">
        <li v-for="(item,x) in tuijian" class="qiangduili">
          <p>{{ item.tuijianming }}</p>
          <span v-for="(item,y) in item.tuijianji" @click="dianji(x,y)" :class="{ active: item.flag }">{{ item.dengji }}</span>
        </li>

        <p class="shuoming" v-html="shuoming"></p>
    </ul>

    <div class="qiangduibottom">
        <div class="left">
            &nbsp<span>0</span>&nbsp</br>Crown
        </div>
        <div class="right">
            <button></button>
            <button @click="quanxuan"></button>
        </div>
    </div>

  </div>
</template>

<script>
    export default {
        name: "shuju",
        data () {
            return {
              "title": "",
              "tuijian": [
                {"id": "1","tuijianming": "",
                  "tuijianji": [
                    {"id": "1","dengji": "5(10)","flag": "true"},
                    {"id": "2","dengji": "4(10)","flag": ""},
                    {"id": "3","dengji": "3(10)","flag": ""},
                    {"id": "4","dengji": "2(10)","flag": ""},
                    {"id": "5","dengji": "1(10)","flag": ""}
                  ]
                },
                {"id": "2","tuijianming": "",
                  "tuijianji": [
                    {"id": "1","dengji": "5(10)","flag": "true"},
                    {"id": "2","dengji": "4(10)","flag": ""},
                    {"id": "3","dengji": "3(10)","flag": ""},
                    {"id": "4","dengji": "2(10)","flag": ""},
                    {"id": "5","dengji": "1(10)","flag": ""}
                  ]
                },
                {"id": "3","tuijianming": "",
                  "tuijianji": [
                    {"id": "1","dengji": "5(10)","flag": "true"},
                    {"id": "2","dengji": "4(10)","flag": ""},
                    {"id": "3","dengji": "3(10)","flag": ""},
                    {"id": "4","dengji": "2(10)","flag": ""},
                    {"id": "5","dengji": "1(10)","flag": ""}
                  ]
                },
                {"id": "4","tuijianming": "",
                  "tuijianji": [
                    {"id": "1","dengji": "5(10)","flag": "true"},
                    {"id": "2","dengji": "4(10)","flag": ""},
                    {"id": "3","dengji": "3(10)","flag": ""},
                    {"id": "4","dengji": "2(10)","flag": ""},
                    {"id": "5","dengji": "1(10)","flag": ""}
                  ]
                },
                {"id": "5","tuijianming": "",
                  "tuijianji": [
                    {"id": "1","dengji": "5(10)","flag": "true"},
                    {"id": "2","dengji": "4(10)","flag": ""},
                    {"id": "3","dengji": "3(10)","flag": ""},
                    {"id": "4","dengji": "2(10)","flag": ""},
                    {"id": "5","dengji": "1(10)","flag": ""}
                  ]
                },
                {"id": "6","tuijianming": "",
                  "tuijianji": [
                    {"id": "1","dengji": "5(10)","flag": "true"},
                    {"id": "2","dengji": "4(10)","flag": ""},
                    {"id": "3","dengji": "3(10)","flag": ""},
                    {"id": "4","dengji": "2(10)","flag": ""},
                    {"id": "5","dengji": "1(10)","flag": ""}
                  ]
                },
                {"id": "7","tuijianming": "",
                  "tuijianji": [
                    {"id": "1","dengji": "5(10)","flag": "true"},
                    {"id": "2","dengji": "4(10)","flag": ""},
                    {"id": "3","dengji": "3(10)","flag": ""},
                    {"id": "4","dengji": "2(10)","flag": ""},
                    {"id": "5","dengji": "1(10)","flag": ""}
                  ]
                }
              ],
              "shuoming": "1.<br>2.<br>3.<br>4./110<br>5./110<br>6.60%<br><br>"
            }
        },
        methods: {
          back(){
            this.$router.go(-1);
          },
          /*
            data
            
            
            
            
            active
            
            
          */
          dianji(x,y){
            var s = 0;
            for (var z = 0; z < this.tuijian[x].tuijianji.length; z++) {
              if (this.tuijian[x].tuijianji[z].flag == 'true') {
                s++;
              }
            }
            for(var i=0; i<this.tuijian.length; i++){
              for(var j=0; j<this.tuijian[i].tuijianji.length; j++){
                if(s=>1){
                  if(i===x && j===y){
                    if(this.tuijian[i].tuijianji[j].flag == 'true'){
                      s--;
                      if(s<1){
                        alert('');
                        return;
                      }
                      this.tuijian[i].tuijianji[j].flag = '';
                    }else{
                      this.tuijian[i].tuijianji[j].flag = 'true';
                    }
                  }
                }
              }
            }
          },
          quanxuan(){
            for(var i=0; i<this.tuijian.length; i++){
              for(var j=0; j<this.tuijian[i].tuijianji.length; j++) {
                this.tuijian[i].tuijianji[j].flag = 'true'
              }
            }
          },
          queren(){
            var xuanxiangbiao = [];
            for(var i=0; i<this.tuijian.length; i++){
              for(var j=0; j<this.tuijian[i].tuijianji.length; j++) {
                if(this.tuijian[i].tuijianji[j].flag == 'true'){
                  var q = i+'-'+j;
                  xuanxiangbiao.push(q);
                }
              }
            }
            alert(xuanxiangbiao);
          }
        }
    }
</script>

<style scoped>

.wrap{
  background: #452E94;
  width:100%;
  height: 1.11rem;
  display:flex;
  justify-content: space-between;
}
.wrap a{
  display:block;
  font-size:0.6rem;
  margin:0.16rem;
  height: 0.56rem;
  width: 1.2rem;
  border-radius:2rem;
  text-align:center;
  background: #452E94;
  color:#e6d9f3;
}
.wrap button{
  display:block;
  font-size:0.28rem;
  margin:0.3rem 0.18rem;
  height: 0.56rem;
  width: 1.5rem;
  border:1px solid #ccc;
  border-radius:1.2rem;
  text-align:center;
  background: #452E94;
  color:#e6d9f3;
}
.content{
  width: 100%;
}
.content h2{
  color: #E0E0EA;
  text-align: center;
  font-size: 0.35rem;
  line-height:1.11rem;
}
.active{
    background: #d9d9d9;
}
.qiangduiul{
  margin-top:0.57rem;
}
.qiangduili{
  display:flex;
  justify-content: space-between;
  font-size: 0.02rem;
  padding: 0 0.2rem;
  margin-top:0.18rem;
}
.qiangduili p{
  font-size: 0.28rem;
  text-align:left;
  padding-left:0.05rem;
}
.qiangduili span{
  width: 1rem;
  text-align:center;
  border: 1px solid #ccc;
  font-size:0.24rem;
}

.shuoming{
  margin-top:1.57rem;
  font-size:0.24rem;
  color: #888;
  padding:0.25rem;
}

.qiangduibottom{
  position:absolute;
  bottom: 0;
  width:100%;
  height: 0.87rem;
  background: #472E93;
}
.left{
  color:#E4E2E6;
  font-size:0.24rem;
  position:absolute;
  left:0.1rem;
  margin-top:0.1rem;
}
.left span{
  color: #DDC120;
}
.right{
  position:absolute;
  right:0.2rem;
}
.right button{
  height: 0.6rem;
  width: 1.56rem;
  line-height: 0.6rem;
  margin-top: 0.14rem;
  font-size: 0.29rem;
  color: #979E95;
  background: #fff;
}
</style>



